var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("原子队长")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Atom船长由作家Joe Gill和艺术家/合作作家Steve Ditko创作，并首次出现在Space Adventures＃33（1960年3月）。 Atom船长最初是为查尔顿漫画公司创建的，但后来被DC Comics收购并修改了DC的危机后 连续性。2011年，DC Comics重新推出了超级英雄漫画，并重新编写了一些角色的历史，包括Atom船长，给他一个新的起源，外观和略微改变的权力。Atom船长是曼哈顿医生的角色灵感，他曾在迷你剧中饰演过（以及后来的真人电影改编）守望者。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        多年来，这个角色已经出现在几个中等到短命的同名系列中，并且是DC的旗舰超级英雄团队正义联盟的几个不同版本的成员。在所有的化身中，角色最初为军队服务。在查尔顿漫画的连续性中，他是一位名叫艾伦·亚当的科学家，当他看似“雾化”，然后他的身体改造，现在作为一个原子动力的存在时，意外地获得了他的能力。在两个DC Comics的化身中，他都是一名名叫Nathaniel Adam的空军飞行员他是科学实验中的一个测试对象，在这个过程中似乎已经解体，后来才重新出现，成为超级动力的Atom船长。多年来，DC一直试图多次重塑这个角色。在一段时期内，这个角色扮演了supervillain Monarch的角色，并且在2005年，DC尝试用一个全新的角色Breach重述了Atom船长的故事，后者随后被丢弃了。在DC 2011年重新启动后的新连续性中，Atom船长从未成为正义联盟的成员，并且该团队对他抱有不信任; 他的性格起源和能力也得到了修改。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Charlton漫画版的Atom船长是Allen Adam。这个角色的起源让亚当在一个特殊的实验火箭中担任技术人员，因为它意外地被困在里面。当火箭在进入高层大气层时爆炸时，亚当被雾化了。然而，他以某种方式获得了超级大国，其中包括在地面上安全地改造自己身体的能力。他穿着红色和黄色的服装，旨在保护人们免受核能的辐射。当他通电时，他的头发变成了银白色。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        后来，在他自己的头衔中，他用他的皮肤下的液态金属装置取代了他原来的红色和金色服装，并在​​他打开电源时转换。Atom船长的能力类似于Gold Key的Doctor Solar和Dell Comics的Nukla等其他核动力超级英雄。凌队长是首次出版，在一系列的短篇小说文集系列太空历险记＃33-40（1960年3月- 1961年6月）和42号（1961年10月）。查尔顿开始在＃75（1965年6月）开始重印他的短篇冒险故事，重新命名为Captain Atom与＃78（1965年12月）并给予英雄全长故事和超级恶魔对抗者，如斯特罗特博士。（以前的故事涉及冷战反共任务或与外国人打交道）。Atom船长后来与超级英雄Nightshade合作，与他分享了一个相互吸引力。超级英雄Blue Beetle主演了初始备份功能，后来被Nightshade备份系列取代。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
